<template>
  <video id="player" class="video-js vjs-big-play-centered" controls preload="auto" :poster="poster || '../assets/stock.jpg'">
    <source :src="src" type="application/x-mpegURL">
  </video>
</template>

<script>
import videojs from 'video.js'
import 'video.js/dist/video-js.min.css'

export default {
  props: {
    src: String,
    poster: String,
  },
  data() {
    return {
      player: null,
    }
  },
  mounted() {
    const options = { fluid: true }

    this.player = videojs('player', options, () => {
      videojs.log('live player is ready!')

      // // In this context, `this` is the player that was created by Video.js.
      this.player.play()

      // How about an event listener?
      this.player.on('ended', function() {
        videojs.log('Awww...over so soon?!')
      })
    })
  },
  destroyed() {
    this.player.dispose()
  }
}
</script>

